function currentLine(text, pos) {
	const start_index = text.slice(0, pos).lastIndexOf('\n');
	if (start_index === -1) {
		return text;
	}
	let end_index = text.slice(pos).indexOf('\n');
	if (end_index === -1) {
		end_index = text.length;
	}
	return text.slice(start_index + 1, pos + end_index);
}

export function currentLineIndent(text, pos) {
	const line_text = currentLine(text, pos);
	return line_text.match(/^(?<indent>\s*)/).groups.indent;
}
