<template>
	<div id="resizable" ref="resizable" :style="styleInfo">
		<Textarea2Pt0 />
	</div>
</template>

<script>
import Textarea2Pt0 from './Textarea/Textarea2Pt0.vue';
export default {
	components: {
		Textarea2Pt0,
	},
	props: {
		border_size: {
			type: Number,
			default: 5,
		},
		epsilon: {
			type: Number,
			default: 30,
		},
		minleft: {
			type: Number,
			default: 30,
		},
		maxleft: {
			type: Number,
			default: 550,
		},
	},
	data() {
		return {
			width: 500,
			height: 300,
			left: 50,
			top: 60,
			movingLeftBorder: false,
		};
	},
	computed: {
		styleInfo() {
			return {
				width: this.width + 'px',
				height: this.height + 'px',
				left: this.left + 'px',
				top: this.top + 'px',
			};
		},
	},
	mounted() {
		const mouseHandler = (e) => {
			// L/R border lit up condition
			if ( (Math.abs(this.left - e.pageX) <= this.epsilon) || this.movingLeftBorder ) {
				if (this.$refs.resizable.style['border-left'] !== '2px solid red') {
					this.$refs.resizable.style['border-left'] = '2px solid red';
				}
				const delta = e.pageX - this.left;
				if (
					( (this.minleft <= this.width - delta &&
					this.width - delta <= this.maxleft) || this.movingLeftBorder ) &&
					e.buttons === 1
				) {
					this.movingLeftBorder = true;
					this.left = e.pageX;
					this.width = this.width - delta;
				} else {
					this.movingLeftBorder = false;
				}
			} else {
				if (this.$refs.resizable.style['border-left'] !== '2px solid black') {
					this.$refs.resizable.style['border-left'] = '2px solid black';
				}
			}
		};

		window.addEventListener('mouseleave', mouseHandler);
		window.addEventListener('mouseenter', mouseHandler);
		window.addEventListener('mouseover', mouseHandler);
		window.addEventListener('mousedown', mouseHandler);
		window.addEventListener('mousemove', mouseHandler);
		window.addEventListener('mouseup', mouseHandler);
	},
};
</script>

<style scoped>
#resizable {
	position: absolute;
	overflow: hidden;
	border: 2px solid black;
}
</style>
