<template>
	<textarea ref="textarea"></textarea>
</template>

<script>
import { currentLineIndent } from './TextareaAuxFxns.js';

export default {
	mounted() {
		this.$refs.textarea.addEventListener('keydown', (e) => {
			if (e.key === 'Tab') {
				e.preventDefault();
				e.stopPropagation();
				const pos = this.$refs.textarea.selectionStart;
				const initstr = this.$refs.textarea.value;
				this.$refs.textarea.value =
					initstr.slice(0, pos) + '\t' + initstr.slice(pos);
				this.$refs.textarea.selectionStart = this.$refs.textarea.selectionEnd =
					pos + 1;
			} else if (e.key === 'Enter') {
				e.preventDefault();
				e.stopPropagation();
				const initstr = this.$refs.textarea.value;
				const pos = this.$refs.textarea.selectionStart;
				const indent = currentLineIndent(initstr, pos);
				this.$refs.textarea.value =
					initstr.slice(0, pos) + '\n' + indent + initstr.slice(pos);
				this.$refs.textarea.selectionStart = this.$refs.textarea.selectionEnd =
					pos + 1 + indent.length;
			}
		});
	},
};
</script>

<style scoped>
textarea {
	width: 100%;
	height: 100%;
	resize: none;
	border: none;
	outline: none;
	box-shadow: none;
}
</style>
